import { Component, OnInit } from '@angular/core';
import { ActionSheetController, MenuController } from '@ionic/angular';
import { Router, ActivatedRoute } from '@angular/router';
import { EventsService } from 'src/app/services/events.service';
import { LocalService } from 'src/app/services/local.service';
import { RestService } from 'src/app/services/rest.service';
import { UtilityService } from 'src/app/services/utility.service';
import { Camera, CameraResultType, CameraSource } from '@capacitor/camera';
import {
  FileTransfer,
  FileUploadOptions,
  FileTransferObject,
} from '@ionic-native/file-transfer/ngx';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-cleanermoredetails',
  templateUrl: './cleanermoredetails.page.html',
  styleUrls: ['./cleanermoredetails.page.scss'],
})
export class CleanermoredetailsPage implements OnInit {
  mode: any;
  reason: any;
  bank: any = {};
  loggedCleaner: any;
  distance: any = '20';
  experience: any = '';

  idImageToUpload: any = null;
  lastIdImage: any = null;
  idExt: any = null;
  addressImageToUpload: any = null;
  lastAddressImage: any = null;
  addressExt: any = null;
  distanceType: any;
  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private menuCtrl: MenuController,
    private actionSheetCtrl: ActionSheetController,
    private transfer: FileTransfer,
    private sanitizer: DomSanitizer,
    private menu: MenuController
  ) {
    this.loggedCleaner = this.localService.getCleaner();
    this.distanceType = this.localService.getSettings().distance_type;
    this.activatedRoute.queryParams.subscribe((params) => {
      if (params.mode) {
        this.mode = params.mode;
      }
      if (this.mode === 'rejected') {
        this.reason = params.reason;
      }
    });
  }

  ngOnInit() {}
  ionViewWillEnter() {
    // disable menu
    this.menuCtrl.enable(false);
  }

  submitVerification() {
    if (!this.lastIdImage || !this.lastAddressImage) {
      return this.utilityService.openAlerts(
        'Error!',
        'Id & address proof required.',
        'ok'
      );
    }
    if (!this.experience || this.experience === '') {
      return this.utilityService.openAlerts(
        'Error!',
        'Enter experience.',
        'ok'
      );
    }
    if (!this.bank.name || this.bank.name === '') {
      return this.utilityService.openAlerts(
        'Error!',
        'Bank Name required.',
        'ok'
      );
    }
    if (!this.bank.beneficiary || this.bank.beneficiary === '') {
      return this.utilityService.openAlerts(
        'Error!',
        'Beneficiary Name required.',
        'ok'
      );
    }
    if (!this.bank.number || this.bank.number === '') {
      return this.utilityService.openAlerts(
        'Error!',
        'Account Number required.',
        'ok'
      );
    }
    if (!this.bank.code || this.bank.code === '') {
      return this.utilityService.openAlerts(
        'Error!',
        'Sort Code required.',
        'ok'
      );
    }

    // first insert records then upload the files
    this.restService
      .postData('cleaner/' + this.loggedCleaner.id + '/updatedocument', {
        distance: this.distance,
        bank: this.bank,
        experience: this.experience,
      })
      .subscribe(
        (data) => {
          // now start uploading one by one
          this.utilityService.loadingOpen('Uploading documents...');
          this.uploadImage();

          setTimeout(() => {
            this.utilityService.loadingClose();
            this.mode = 'pending';
          }, 15000);

          console.log(data);
        },
        (err) => {
          console.log(err);
          this.utilityService.openAlerts(
            'Error!',
            'Something went wrong, please try again later.',
            'ok'
          );
        }
      );
  }

  resubmitVerification() {
    this.mode = 'new';
  }

  // image uploading section =========================

  async takePicture(type) {
    // Get the data of an image
    const image = await Camera.getPhoto({
      quality: 80,
      allowEditing: false,
      resultType: CameraResultType.Uri,
    });

    if (type === 'id') {
      this.idImageToUpload = image.path;
      this.lastIdImage = this.sanitizer.bypassSecurityTrustResourceUrl(
        image && image.webPath
      );
      this.idExt = image.format;
    } else {
      this.addressImageToUpload = image.path;
      this.lastAddressImage = this.sanitizer.bypassSecurityTrustResourceUrl(
        image && image.webPath
      );
      this.addressExt = image.format;
    }
  }

  async uploadImage() {
    const fileTransfer: FileTransferObject = this.transfer.create();
    const options1 = {
      fileKey: 'image',
      fileName: `file-id-${this.loggedCleaner.id}.${this.idExt}`,
      chunkedMode: false,
      mimeType: 'multipart/form-data',
      params: {
        fileName: `file-id-${this.loggedCleaner.id}.${this.idExt}`,
        image: `file-id-${this.loggedCleaner.id}.${this.idExt}`,
      },
    };

    fileTransfer
      .upload(
        this.idImageToUpload,
        this.restService.baseURL +
          'cleaner/upload_cleaner_id/' +
          this.loggedCleaner.id,
        options1
      )
      .then(
        (data) => {
          console.log(data);
          // clear the last data
          this.lastIdImage = null;
          this.idImageToUpload = null;
        },
        (err) => {
          console.log(err);
        }
      );

    const options2 = {
      fileKey: 'image',
      fileName: `file-address-${this.loggedCleaner.id}.${this.idExt}`,
      chunkedMode: false,
      mimeType: 'multipart/form-data',
      params: {
        fileName: `file-address-${this.loggedCleaner.id}.${this.idExt}`,
        image: `file-address-${this.loggedCleaner.id}.${this.idExt}`,
      },
    };

    fileTransfer
      .upload(
        this.addressImageToUpload,
        this.restService.baseURL +
          'cleaner/upload_cleaner_address/' +
          this.loggedCleaner.id,
        options2
      )
      .then(
        (data) => {
          console.log(data);
          // clear the last data
          this.lastAddressImage = null;
          this.addressImageToUpload = null;
        },
        (err) => {
          console.log(err);
        }
      );
  }

  logout() {
    this.localService.removeUser();
    this.localService.removeCleaner();

    this.menu.enable(false);
    // refresh the menu : send event
    this.eventsService.publish('menu', 'guest');
    this.router.navigateByUrl('/login');
  }
}
